import React, { Fragment, useState } from "react";
import ProductCard from "../../../../components/features/product";
import { addToCart } from "../../../../actions/cart";
import ChooseVariationModal from "../../../../components/features/modal/choose-variation";
import { Splide, SplideSlide } from "@splidejs/react-splide";
import { collectionVibo } from "../../../../components/options";
import ShimmerProducts from "../../../../components/features/loaders/ShimmerProducts";

function BlockCollection({
	double = true,
	variations,
	collectionSize = "col-4 col-lg-3 col-md-3"
}) {
	let simpleVariation = variations;
	const [modal, setModal] = useState({
		open: false,
		product: { variation: { id: 0 } }
	});

	function onAddToCart(dataProduct) {
		setModal({
			open: true,
			product: dataProduct
		});
	}

	const toMatrix = (arr, width = 2) =>
		arr &&
		arr.reduce(
			(rows, key, index) =>
				(index % width === 0
					? rows.push([key])
					: rows[rows.length - 1].push(key)) && rows,
			[]
		);

	variations = toMatrix(variations);

	if (!variations) {
		return (
			<div className="pl-4">
				<ShimmerProducts columns={3} option={collectionVibo} />
			</div>
		);
	}

	return (
		<>
			<div className="desktop-collection only-desktop">
				{variations.slice(0, 4).map((row, index) => (
					<Fragment key={index}>
						{row.map((variations, index) => (
							<div
								className={`${
									index % 2 === 0 && window.innerWidth < 560
										? `pl-0`
										: ``
								} ${collectionSize}`}
								key={index}
							>
								<ProductCard
									product={variations}
									type="sidebar"
									onAddToCart={dataProduct => {
										onAddToCart(dataProduct);
									}}
								/>
							</div>
						))}
					</Fragment>
				))}
			</div>

			<div className="mobile-collection only-mobile">
				<Splide className="splide" options={collectionVibo}>
					{double
						? variations.map((row, index) => (
								<SplideSlide
									className="double-product"
									key={index}
								>
									{row &&
										row.map((product, index) => {
											return (
												<Fragment key={index}>
													<ProductCard
														product={product}
														type="sidebar"
														onAddToCart={dataProduct => {
															onAddToCart(
																dataProduct
															);
														}}
													/>
												</Fragment>
											);
										})}
								</SplideSlide>
						  ))
						: simpleVariation.map((product, index) => (
								<SplideSlide
									className="double-product"
									key={index}
								>
									<ProductCard
										product={product}
										type="sidebar"
										onAddToCart={dataProduct => {
											onAddToCart(dataProduct);
										}}
									/>
								</SplideSlide>
						  ))}
				</Splide>
			</div>

			<ChooseVariationModal
				setModal={setModal}
				modal={modal}
				addToCart={addToCart}
			/>
		</>
	);
}

export default BlockCollection;
