import React, { useMemo, useState } from "react";
import { Helmet } from "react-helmet";
import { useSelector } from "react-redux";
import BlockBannerCollection from "../components/blocks/BlockBannerCollection";
import CollectionsWithTab from "../components/collections/CollectionsWithTab";
import { getImageUrl } from "../../../utils/oxMethods";
import BannerSlider from "../components/banners/bannerSlider";
import Banner from "../components/banners/Banner";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import LatestCollectionThree from "../components/collections/CollectionsThree";
import market from "../../../config/market";

function HomePageEight() {
	const marketData = useSelector((state) => state.marketData);
	const banners = useSelector((state) => state.any?.banners);
	const { blocks } = useSelector((state) => state.blocks);
	const [showText, setShowText] = useState(false);
	const { t } = useTranslation();
	const description = marketData?.settings?.seoHtml;
	const head = useMemo(() => {
		return {
			title: marketData.name,
			description: marketData.settings && marketData.settings.description,
			keyword: marketData.settings && marketData.settings.keyword,
			favicon: getImageUrl(marketData.settings.faviconImage, "50x_"),
		};
	}, []);

	if (!blocks) {
		return "";
	}

	const bigBlocks = blocks.find((block) => block.title === "big_block");
	const bigBlocksTwo = blocks.find(
		(block) => block.title === "big_block_two"
	);
	const top_sale = blocks.find((block) => block.title === "top_sale");

	const bannerTop = banners.filter((banner) => banner?.position === "top");
	const bannerCenter = banners.filter(
		(banner) => banner.position === "center"
	);
	const bannerBottom = banners.filter(
		(banner) => banner.position === "bottom"
	);

	return (
		<>
			<Helmet>
				<title>{head.title}</title>
				<meta name="description" content={head.description} />
				<meta name="keywords" content={head.keyword} />
				<meta name="apple-mobile-web-app-title" content={head.title} />
				<meta name="application-name" content={head.title} />
				<link
					rel="icon"
					type="image/png"
					href={head.favicon}
					sizes="16x16"
				/>
			</Helmet>
			<h1 className="d-none">{head.title}</h1>

			<div className="home-page-eight">
				{bannerTop ? (
					<div className="container">
						<BannerSlider banner={bannerTop} position={"top"} />
					</div>
				) : null}

				<div className="container banners-container">
					{bannerBottom.slice(0, 2).map((item, i) => (
						<Banner
							item={item}
							key={i}
							size={"original"}
							shimmerHeight="245px"
						></Banner>
					))}
				</div>

				<div className="collection-tabs container">
					<CollectionsWithTab blocks={blocks} />
				</div>
				<div className="top-sale container">
					<LatestCollectionThree
						collection={top_sale}
						title={top_sale?.name}
						width={1}
					/>
				</div>

				<div className="container banners-container">
					{bannerBottom.slice(2, 4).map((item, i) => (
						<Banner item={item} key={i} size={"original"}></Banner>
					))}
				</div>
				<div className="container menu-banner-collection">
					<BlockBannerCollection
						block={bigBlocks}
						menuCol={"col-lg-2 col-12"}
						bannerCol={"col-0"}
						collectionCol={"col-lg-10 col-12"}
						collectionSize={"col-6 col-lg-3 col-md-4"}
						doubleCollection={false}
					/>
				</div>
				<div className="container banners-container">
					{bannerBottom.slice(4, 6).map((item, i) => (
						<Banner item={item} key={i} size={"original"}></Banner>
					))}
				</div>
				<div className="container menu-banner-collection">
					<BlockBannerCollection
						block={bigBlocksTwo}
						menuCol={"col-lg-2 col-12"}
						bannerCol={"col-0"}
						collectionCol={"col-lg-10 col-12"}
						collectionSize={"col-6 col-lg-3 col-md-4"}
						doubleCollection={false}
					/>
				</div>

				<div className="container mini-sliders-container">
					<h3 className="title">
						Популярные бренды
						<Link to="/trademarks">
							{t("all-trademarks")}{" "}
							<img
								src={market.config.icons.rArrow}
								alt="brands"
							/>
						</Link>
					</h3>
					<BannerSlider banner={bannerCenter} position={"center"} />
				</div>
				<div className="container text-container">
					<div
						className={`before-footer-text ${
							showText ? "active" : ""
						}`}
						dangerouslySetInnerHTML={{ __html: description }}
					/>
					<Link
						to="#"
						className="opener"
						onClick={() => setShowText(!showText)}
					>
						{showText ? t("hide") : t("more")}
					</Link>
				</div>
			</div>
		</>
	);
}

export default HomePageEight;
