import { Splide, SplideSlide } from "@splidejs/react-splide";
import React from "react";
import SimpleShimmer from "../../../../components/features/loaders/SimpleShimmer";
import MediaImage from "../../../../components/features/product/MediaImage";

export default function BannerSlider({ banner, position, options }) {
	if (!banner) return <SimpleShimmer height="100px" />;

	return (
		<div className="banner-slider">
			<Splide
				options={
					options
						? options
						: {
								pagination: false,
								perPage: position === "center" ? 6 : 4,
								focus: "left",
								arrows: true,
								gap: "20px",
								breakpoints: {
									991: {
										perPage: position === "center" ? 6 : 2
									},
									768: {
										perPage: position === "center" ? 6 : 2
									},
									560: {
										perPage: position === "center" ? 3 : 1
									}
								}
						  }
				}
			>
				{banner.map((item, i) => {
					return (
						<SplideSlide style={{ padding: "10px" }} key={i}>
							<MediaImage
								image={item.image}
								largeSize={"800x_"}
								mediumSize={"800x_"}
								smallSize={"800x_"}
								shimmerHeight="120px"
							/>
						</SplideSlide>
					);
				})}
			</Splide>
		</div>
	);
}
