import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import BlockCollections from "../collections/BlockCollections";
import { dt } from "../../../../i18n";
import { useTranslation } from "react-i18next";
import Slider from "../banners/Slider";
import MediaImage from "../../../../components/features/product/MediaImage";
import market from "../../../../config/market";
const BlockBannerCollection = ({
	block,
	menuCol,
	bannerCol,
	collectionCol,
	collectionSize,
	doubleCollection,
}) => {
	const { menuList } = useSelector((state) => state.menu);
	const [variations] = useState(block?.products);

	const [menu, setMenu] = useState([]);
	const { t } = useTranslation();

	useEffect(() => {
		if (block) {
			if (menuList && menuList.length > 0 && block.menus.length > 0) {
				setMenu(
					block.menus.map((m) => {
						return (m = menuList.find(
							(menu) => menu.id === parseInt(m)
						));
					})
				);
			}
		}
	}, []);

	let banner = block?.dynamicCollectionBlock.find((d) => d.name === "banner");
	let image = banner?.photos[0];
	let underMenuBanner = banner?.photos[1];

	if (!block) {
		return "";
	}

	return (
		<div className="block-banner-collection-wrapper">
			<div className="block-banner-collection">
				<div className={`block-menus ${menuCol}`}>
					<h3 className="title">{dt(block.name)}</h3>
					{menu.length > 0 ? (
						<>
							<ul className="block-menu-list">
								{menu.slice(0, 11).map(
									(item, index) =>
										item && (
											<li
												className="block-menu-item"
												key={index}
											>
												<Link
													to={
														"/category/" + item.slug
													}
												>
													{dt(item.name)}
													<img
														src={
															market.config.icons
																.rArrow
														}
														className="d-none"
														alt="menu"
													/>
												</Link>
											</li>
										)
								)}
							</ul>
							<div className="under-menu-banner d-none">
								<MediaImage
									image={underMenuBanner}
									largeSize={"500x_"}
									mediumSize={"300x_"}
									smallSize={"50x_"}
									alt={"banner"}
								/>
							</div>
							<a
								href={block.footerTitle}
								className="see-more d-none"
							>
								<span>{t("see-all")}</span>
								<img
									src="/assets/images/vibo/arrow.png"
									alt="arrow"
									width="35"
									height="20"
								/>
							</a>
						</>
					) : null}
				</div>

				<div className={`${bannerCol} block-banner only-desktop`}>
					<Link to={image?.link}>
						<MediaImage
							image={image}
							largeSize={"500x_"}
							mediumSize={"500x_"}
							smallSize={"500x_"}
							alt={"banner"}
						/>
					</Link>
				</div>
				<div className={`${collectionCol} block-collection`}>
					<BlockCollections
						double={doubleCollection}
						variations={variations}
						collectionSize={collectionSize}
					/>
				</div>
			</div>

			<div className="block-slider">
				{block.dynamicCollectionBlock.map(
					(c, i) =>
						c.name === "slide" && (
							<Slider
								key={i}
								slider={c.photos}
								size={"original"}
							/>
						)
				)}
			</div>
		</div>
	);
};

export default BlockBannerCollection;
